










































import { reactive, defineComponent } from "@vue/composition-api";
import { userStore } from "@/store";

export default defineComponent({
  name: "Login",

  setup(props, { root }) {
    const form = reactive({
      username: "",
      password: ""
    });

    function login() {
      userStore.mutations
        .login(form)
        .then(() => root.$router.push("/"))
        .catch(err => {
          root.$toasted.error("Error logging in: " + err.message);
        });
    }

    return {
      form,
      login
    };
  }
});
